.navbar {
  background-color: #ffffff;
  border-bottom: 1px solid #eaeaea;
  padding: 10px 20px;
  position: sticky;
  top: 0;
  z-index: 1000;
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: auto;
}

.navbar-logo a {
  font-size: 1.5rem;
  font-weight: bold;
  color: #333;
  text-decoration: none;
}

.burger {
  display: none;
  font-size: 24px;
  cursor: pointer;
}

.navbar-links {
  display: flex;
  list-style: none;
  gap: 20px;
}

.navbar-links li a {
  text-decoration: none;
  color: #333;
  font-weight: 500;
}

.navbar-links li a:hover {
  color: #0070f3;
}

/* Mobile */
@media (max-width: 768px) {
  .burger {
    display: block;
  }

  .navbar-links {
    flex-direction: column;
    display: none;
    margin-top: 10px;
    background: #f9f9f9;
    padding: 15px;
    border-radius: 8px;
  }

  .navbar-links.active {
    display: flex;
  }
}
