/* ======== Base générale ======== */
body {
  margin: 0;
  font-family: 'Inter', sans-serif;
  background-color: #f6f7fb;
  color: #1c1e21;
  line-height: 1.6;
}

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* ======== Boutons ======== */
button {
  font-family: inherit;
  background-color: #0070ba;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 6px;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #005ea6;
}

button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

/* ======== Liens ======== */
a {
  color: #0070ba;
  text-decoration: none;
  font-weight: 500;
}

a:hover {
  text-decoration: underline;
}

/* ======== Titres ======== */
h1, h2, h3, h4 {
  color: #1c1e21;
  margin-bottom: 10px;
}

/* ======== Sections ======== */
section {
  padding: 40px 20px;
  background-color: white;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  margin: 20px auto;
  max-width: 1000px;
}

/* ======== Inputs ======== */
input[type="text"],
input[type="email"],
input[type="password"],
input[type="date"],
input[type="time"],
select {
  width: 100%;
  padding: 12px;
  border-radius: 6px;
  border: 1px solid #ccc;
  margin-bottom: 15px;
  font-size: 16px;
  box-sizing: border-box;
}

input:focus,
select:focus {
  outline: none;
  border-color: #0070ba;
  box-shadow: 0 0 0 2px rgba(0, 112, 186, 0.2);
}

/* ======== Layout Utilitaire ======== */
.centered {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 300px;
}

.text-center {
  text-align: center;
}

.mt-2 { margin-top: 20px; }
.mt-4 { margin-top: 40px; }

