.about-container {
  padding: 40px 20px;
  font-family: 'Inter', sans-serif;
  color: #222;
}

.about-container img {
  max-width: 150px;
  margin-bottom: 20px;
}

.about-container h1 {
  font-size: 2.2em;
  color: #28a745;
  margin-bottom: 15px;
}

.about-container p {
  font-size: 1.1em;
  line-height: 1.6;
  margin-bottom: 15px;
}

.about-container ul {
  margin-top: 15px;
  padding-left: 20px;
}

.about-container li {
  margin-bottom: 10px;
}
